@use 'base/variables' as v;

.citas {
    list-style: none;
    padding: 0;
    li {
        padding: 1rem;
        border-top: 4px solid v.$gris-oscuro;
        transition: all ease 1s;
        margin: 1rem 0;
        &:first-of-type {
            border: none;
        }
        p {
            color: v.$azul;
            font-weight: 900;
        }
        span {
            color: v.$blanco;
        }
        h3 {
            font-weight: 900;
            font-size: 3.5rem;
        }
        &:hover {
            background-color: darken(v.$gris, 2%);
            span {
                color: v.$negro;
            }
            h3 {
                color: v.$azul;
            }
        }
    }
}
.dialibre {
    h2 {
        margin-top: 10rem;
        color: v.$naranja;
        font-weight: 900;
    }
}
.serviciosa {
    @extend .citas;
    li {
        border-top: 4px solid v.$gris-oscuro;
        border-bottom: 4px solid v.$gris-oscuro;
        &:first-of-type {
            border-top: 4px solid v.$gris-oscuro;
            border-bottom: 4px solid v.$gris-oscuro;
        }
    }
}