@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario {
    width: 100%;
    &__campo {
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        margin-top: 1rem;
        label {
            margin-bottom: .7rem;
        }
        input {
            border: none;
            padding: 1rem;
            border-radius: .5rem;
            &:disabled {
                background-color: lighten(v.$negro, 30%);
                color: v.$blanco;
                cursor: not-allowed;
            }
        }
    }
    @include m.desktop { 
        &__campo {
            flex-direction: row;
            align-items: center;
            label {
                flex: 0 0 10rem;
                margin-bottom: 0;
            }
            input {
                flex: 1;
            }
        }
    }
    &__boton {
        @include m.boton(v.$blanco, v.$negro);
        margin: 0 auto;
        margin-top: 2.5rem;
        border-radius: 0;
        width: 25rem;
        @include m.tablet { 
            width: 25rem;   
        }
    }
}