@use 'base/variables' as v;
@use 'base/mixins' as m;

.crud-volver {
    @include m.boton(v.$blanco, v.$negro);
    border-radius: 0;
    margin-bottom: 2rem;
    @include m.tablet { 
        width: 15rem;   
    }
}
.boton-crud {
    @include m.boton(v.$azul, v.$blanco);
    width: 20rem;
    border-radius: .3rem;
    margin: 0 auto;
    margin-top: 3rem;
    padding: 1.5rem .5rem;
}
.acciones {
    display: flex;
    justify-content: space-between;
    input {
        margin-top: 1rem;
    }
}
.boton-acciones {
    @include m.boton(v.$azul, v.$blanco);
    border-radius: 0;
    width: 25rem;
}
.boton-accionesb {
    @include m.boton(v.$rojo, v.$blanco);
    border-radius: 0;
    width: 25rem;
}