@use 'base/variables' as v;
@use 'base/mixins' as m;

.seccion {
    &__heading {
        font-weight: 900;
    }
    &__servicio {
        border-bottom: 1px solid v.$gris-oscuro;
        padding: 1rem 0;
        font-weight: 900;
        &:last-of-type {
            margin-bottom: 2rem;
        }
    }
    p {
        span {
            font-weight: 900;
            color: v.$azul;
        }
    }
    &__boton {
        @include m.boton(v.$azul, v.$blanco);
        width: 20rem;
        border-radius: .3rem;
        margin: 0 auto;
        margin-bottom: 3rem;
        padding: 1.5rem .5rem;
    }
}