@use 'base/mixins' as m;
@use 'base/variables' as v;

.login {
    @include m.tablet { 
        &__texto {
            padding: 5rem 0;
        }
    }
    &__heading {
        font-weight: bold;
    }
    &__descripcion {
        text-align: center;
        font-size: 2rem;
    }
}
.acciones {
    padding: 5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.desktop { 
        flex-direction: row;
        justify-content: space-evenly;
    }
    a {
        color: v.$blanco;     
        margin-bottom: 1.5rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.desktop { 
            margin-bottom: 0;   
        }   
    }
}