@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra {
    font-weight: 900;
    display: flex;
    flex-direction: column;
    @include m.tablet { 
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    p {
        font-size: 2rem;
        margin-top: 0;
        text-align: center;
        @include m.tablet { 
            text-align: justify;   
            margin-right: .5rem;
        }
    }
    &__logout {
        @include m.boton(v.$opaco, v.$blanco);
        padding: 1rem;
        border-radius: 0;
        margin-bottom: 2rem;
    }
    &__secciones {
        @include m.desktop { 
            display: flex;
            justify-content: space-between;   
        }
    }
    &__botones {
        @include m.boton(v.$blanco, v.$negro);
        width: auto;
        margin-bottom: .5rem;
        border-radius: 0;
        @include m.desktop { 
            max-width: 30rem;
            min-width: 15rem;
        }
    }
}
